'use client';

import { useState, useEffect } from 'react';
import { adminApi } from '@/lib/api';

interface FileInfo {
  name: string;
  size: string;
}

export default function FileListSection() {
  const [files, setFiles] = useState<FileInfo[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [filterType, setFilterType] = useState<'all' | 'repair' | 'config'>('all');

  const fetchFiles = async () => {
    setIsLoading(true);
    try {
      const fileList = await adminApi.getFileList();
      setFiles(fileList);
    } catch (error) {
      console.error('„Éï„Ç°„Ç§„É´‰∏ÄË¶ß„ÅÆÂèñÂæó„Å´Â§±Êïó„Åó„Åæ„Åó„Åü:', error);
      setFiles([]);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchFiles();
  }, []);

  // „Éï„Ç°„Ç§„É´„Çí„Éï„Ç£„É´„Çø„É™„É≥„Ç∞
  const filteredFiles = files.filter((file) => {
    // Ê§úÁ¥¢„ÇØ„Ç®„É™„Åß„Éï„Ç£„É´„Çø
    if (searchQuery && !file.name.toLowerCase().includes(searchQuery.toLowerCase())) {
      return false;
    }
    
    // „Çø„Ç§„Éó„Åß„Éï„Ç£„É´„Çø
    if (filterType === 'repair') {
      // ‰øÆÁêÜÈñ¢ÈÄ£„Éï„Ç°„Ç§„É´Ôºà„Ç´„ÉÜ„Ç¥„É™Âêç„ÅåÂê´„Åæ„Çå„Çã„Éï„Ç°„Ç§„É´Ôºâ
      const repairKeywords = ['„Ç®„Ç¢„Ç≥„É≥', '„Éê„ÉÉ„ÉÜ„É™„Éº', '„Éà„Ç§„É¨', 'Ê∞¥ÈÅì', '„Éâ„Ç¢', 'Á™ì', 'ÂÜ∑ËîµÂ∫´', '„Ç¨„Çπ', '„ÇΩ„Éº„É©„Éº', '„Çø„Ç§„É§', '„Éí„É•„Éº„Ç∫', '„É™„É¨„Éº', '„Éô„É≥„ÉÅ„É¨„Éº„Çø„Éº', '„É´„Éº„Éï', 'Â§ñÈÉ®ÈõªÊ∫ê', 'LED', 'ÂÆ∂ÂÖ∑', 'ÊéíÊ∞¥', 'Áï∞Èü≥', '„Ç¶„Ç§„É≥„Éâ„Ç¶', '„Ç§„É≥„Éê„Éº„Çø„Éº', 'FF„Éí„Éº„Çø„Éº'];
      return repairKeywords.some(keyword => file.name.includes(keyword));
    } else if (filterType === 'config') {
      // Ë®≠ÂÆö„Éï„Ç°„Ç§„É´
      return file.name.includes('env') || file.name.includes('config') || file.name.includes('requirements') || file.name.includes('backup');
    }
    
    return true;
  });

  return (
    <div className="bg-gray-50 rounded-lg p-6 border-l-4 border-blue-500">
      <h2 className="text-xl font-bold text-gray-800 mb-4">üìÅ „Éï„Ç°„Ç§„É´ÁÆ°ÁêÜ</h2>
      <p className="text-gray-600 mb-4">ÁèæÂú®Ë™≠„ÅøËæº„Åæ„Çå„Å¶„ÅÑ„Çã„Éï„Ç°„Ç§„É´‰∏ÄË¶ßÔºö</p>

      {/* Ê§úÁ¥¢„Å®„Éï„Ç£„É´„Çø */}
      <div className="mb-4 space-y-2">
        <input
          type="text"
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          placeholder="„Éï„Ç°„Ç§„É´Âêç„ÅßÊ§úÁ¥¢..."
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
        />
        <div className="flex gap-2">
          <button
            onClick={() => setFilterType('all')}
            className={`px-4 py-2 rounded-lg text-sm font-semibold transition-colors ${
              filterType === 'all'
                ? 'bg-blue-500 text-white'
                : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
            }`}
          >
            „Åô„Åπ„Å¶ ({files.length})
          </button>
          <button
            onClick={() => setFilterType('repair')}
            className={`px-4 py-2 rounded-lg text-sm font-semibold transition-colors ${
              filterType === 'repair'
                ? 'bg-blue-500 text-white'
                : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
            }`}
          >
            ‰øÆÁêÜÈñ¢ÈÄ£
          </button>
          <button
            onClick={() => setFilterType('config')}
            className={`px-4 py-2 rounded-lg text-sm font-semibold transition-colors ${
              filterType === 'config'
                ? 'bg-blue-500 text-white'
                : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
            }`}
          >
            Ë®≠ÂÆö„Éï„Ç°„Ç§„É´
          </button>
        </div>
      </div>

      <div className="bg-white border border-gray-200 rounded-lg p-4 mb-4">
        {isLoading ? (
          <div className="text-gray-500">Ë™≠„ÅøËæº„Åø‰∏≠...</div>
        ) : filteredFiles.length === 0 ? (
          <div className="text-gray-500">
            {searchQuery || filterType !== 'all' 
              ? 'Êù°‰ª∂„Å´‰∏ÄËá¥„Åô„Çã„Éï„Ç°„Ç§„É´„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì' 
              : '„Éï„Ç°„Ç§„É´„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì'}
          </div>
        ) : (
          <div className="space-y-2">
            <div className="text-sm text-gray-600 mb-2">
              Ë°®Á§∫‰∏≠: {filteredFiles.length}‰ª∂ / ÂÖ®{files.length}‰ª∂
            </div>
            {filteredFiles.map((file, index) => (
              <div
                key={`${file.name}-${index}`}
                className="flex justify-between items-center py-2 border-b border-gray-100 last:border-b-0 hover:bg-gray-50 transition-colors"
              >
                <span className="font-semibold text-gray-800">{file.name}</span>
                <span className="text-gray-500 text-sm">{file.size}</span>
              </div>
            ))}
          </div>
        )}
      </div>

      <button
        onClick={fetchFiles}
        disabled={isLoading}
        className="bg-gradient-to-r from-blue-500 to-blue-600 text-white px-6 py-3 rounded-lg font-bold hover:shadow-lg transition-all disabled:opacity-50 disabled:cursor-not-allowed"
      >
        üîÑ „Éï„Ç°„Ç§„É´‰∏ÄË¶ßÊõ¥Êñ∞
      </button>
    </div>
  );
}

